<script>
    export let isActive;
    let _class = "";
    export { _class as class };
</script>

<button
    class={`button-wrapper ${_class} ${isActive ? "is-active" : ""}`}
    on:click
    {...$$restProps}
>
    <div>
        <slot />
    </div>
</button>

<style lang="scss">
    $button-purple: #483f8e;
    button > div {
        // Matches the .button-wrapper border-image
        background-color: $button-purple;
        display: flex;
        justify-content: center;
        align-items: center;
    }
</style>
