<script>
    export let text;
    export let isActive;
    export let doLocalizeText = true;
    let _class = "";
    export { _class as class };
</script>

<button
    class={`button-wrapper ${_class} ${isActive ? "is-active" : ""}`}
    on:click
    {...$$restProps}
>
    <div class="button-inner">
        {doLocalizeText ? globalThis.i18n(text) : text}
    </div>
</button>

<style>
    .button-inner {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
    }
</style>
