    width: 250px;
    font-family: sans-serif;
  }
  // ------------------------
  .height100 {
    height: 100%;
  }
  .overflowyscroll {
    overflow-y: scroll;
  }
  .flex {
    display: flex;
  }
  .flex-direction-column {
    flex-direction: column;
  }
  .align-items-center {
    align-items: center;
  }
  .gap-20 {
    gap: 20px;
  }
  .gap-15 {
    gap: 15px;
  }
  $button-purple: #483f8e;
  // Remove default button styling
  .button-wrapper {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    color: white;
    text-align: center;
    width: 100%;
    max-width: 300px;
  }
  .button-wrapper.sm {
    max-width: 120px;
  }
  .button-wrapper {
    border: 16px solid;
    border-image-outset: 0;
    border-image-repeat: stretch;
    border-image-slice: 250;
    border-image-source: url("/ui/Assets_Icon_Border_QtrCircle_2_Purple.png");
    border-image-width: 1;
    transition: 0.2s;
    // Allow button to scale without blurring. Credit: https://stackoverflow.com/a/16878602/4418836
    backface-visibility: hidden;
